/* src/component.css */
body {
    margin: 0;
    font-family: Arial, sans-serif;
    background: linear-gradient(135deg, #f3ec78, #af4261);
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
  }
  
  .form {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .leftabc {
    width: 300px;
    margin-right: 10px;
  }
  
  .box {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
  }
  
  .card {
    width: 18rem;
    margin: 10px;
  }
  
  .navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    background-color: rgba(0, 0, 0, 0.7);
  }
  
  .navbar .title {
    color: white;
    font-size: 1.5rem;
    margin-left: 10px;
  }
  
  .btn-outline-success {
    color: white;
    border-color: white;
  }
  
  .btn-outline-success:hover {
    background-color: white;
    color: black;
  }
  